.table {
    border-collapse: collapse;
    font-size: var(--font-size-S);
    width: 100%;
}

.table td,
.table th {
    padding: 10px;
    border-bottom: var(--block-border);
}
.table th {
    text-align: left;
}

.table th:first-child {
    text-align: center;
    width: 50px;
}

.pre {
    white-space: pre-wrap;
}
.logLink {
    display: block;
}
